//Eventos de clicks
function eventListeners() {

  menuBtn.addEventListener('click', navegacion);
  pantallaCompleta.addEventListener('click', pantalla);

  //modo oscuro
  iconosModo.addEventListener('click', modoOscuro);
  iconosModo.addEventListener('click', localModoOscuro);

  //Formulario checkbox
  const metodoContacto = document.querySelectorAll('input[name="contacto[sitio-cuentas]"]');
  metodoContacto.forEach(input => input.addEventListener('click', seleccionarMetodo));

  //Ventana modal
  const btnCerrarVentana = document.querySelector(".btnCerrarVentana");
  btnCerrarVentana.addEventListener('click', ventanaModal);

}

function navegacion() {

    menuBtn.classList.toggle("open");
    nav.classList.toggle("muestra");
    body.classList.toggle("fijar-body");
    pantallaCompleta.classList.toggle("activarPantallaNegra");
    
}

function pantalla() {

  menuBtn.classList.remove("open");
  nav.classList.remove("muestra");
  body.classList.remove("fijar-body");
  pantallaCompleta.classList.remove("activarPantallaNegra");
    
}


//Metodo de formulario checkbox
function seleccionarMetodo(e) {
  const inputSitio = document.querySelector('#input-sitio');


  if(e.target.value === 'si') {
      inputSitio.innerHTML = `
      <div class="form-input">
        <label for="sitio">¿Cuál es el nombre de tu sitio web?:<span>*</span></label>
        <input 
        required 
        
        maxlength="50" 
        title="Escriba el nombre de su sitio web, máximo 50 caracteres" 
        autocomplete="off"  
        type="text" 
        id="sitio" 
        placeholder="https://tusitioweb.com/" 
        name="contacto[sitio]">
        <div class="validacion-error">
            <p><i class="fas fa-exclamation-circle"></i>Escriba el nombre de su sitio web</p>
        </div>
    </div>

      `;
  } else {
    inputSitio.innerHTML = `
    
    `;
}
}

function ventanaModal() {
  
  const ventanaContacto = document.querySelector(".ventanaContacto");

  ventanaContacto.classList.remove("mostrarVentana");
  body.classList.remove("fijar-body");
  
}

function modoOscuro() {


  document.querySelector(".sun-logo").classList.toggle("animate-sun");
  document.querySelector(".moon-logo").classList.toggle("animate-moon");
  // document.querySelector("body").classList.toggle("dark");

  // const iconosModo = document.querySelector(".btn-modo-oscuro");
  // iconosModo.addEventListener('click', modoOscuro);

  
  

}

function localModoOscuro() {
  // const iconosModo = document.querySelector('.btn-modo-oscuro');
  // const workContainer = document.getElementById('work');
  
  
  body.classList.toggle('dark'); //cambiar el cuerpo del HTML por la clase 'dark'
  iconosModo.classList.toggle('active');//cambiar el botón HTML con el id='switch' con la clase 'active'
  // workContainer.classList.toggle('dark');

  if(body.classList.contains('dark')){ //cuando el cuerpo tiene la clase 'dark' actualmente
    localStorage.setItem('darkMode', 'enabled'); //almacenar estos datos si el modo oscuro está activado
  }else{
  localStorage.setItem('darkMode', 'disabled'); //almacenar estos datos si el modo oscuro está desactivado
  }
  
  
  if(localStorage.getItem('darkMode') == 'enabled'){
    body.classList.add('dark');
    iconosModo.classList.add('active');
  // workContainer.classList.toggle('dark');
  // alert('hola mundo');
  }
}



